import React from "react";
import DetailsTop from "./DetailsTop";
import styled from "styled-components";
import JobInfo from "./JobInfo";
import DetailsFooter from "./DetailsFooter";

const Container = styled.section`
    background-color: ${(props) =>
        props.theme === "light" ? "##F2F2F2" : "#121721"};
    margin-inline: 6.4%;

    @media all and (min-width: 768px) {
        margin-inline: 5.2%;
    }

    @media all and (min-width: 1440px) {
        margin-inline: 24%;
    }
`;

export default function Details(props) {
    console.log(props.activeJob[0].postedAt);
    return (
        <Container theme={props.theme}>
            <DetailsTop activeJob={props.activeJob} theme={props.theme} />
            <JobInfo
                theme={props.theme}
                postedAt={props.activeJob[0].postedAt}
                contract={props.activeJob[0].contract}
                position={props.activeJob[0].position}
                company={props.activeJob[0].company}
                location={props.activeJob[0].location}
                href={props.activeJob[0].apply}
                description={props.activeJob[0].description}
                requirementsContent={props.activeJob[0].requirements.content}
                requirementsItems={props.activeJob[0].requirements.items}
                roleContent={props.activeJob[0].role.content}
                roleItems={props.activeJob[0].role.items}
            />
            <DetailsFooter
                position={props.activeJob[0].position}
                theme={props.theme}
                href={props.activeJob[0].apply}
                value="Apply now"
            />
        </Container>
    );
}
