import React, { useState } from "react";
import Header from "./components/Header";
import Details from "./components/Details";
import styled from "styled-components";
import Main from "./components/Main";

import jobs from "./data.json";

import "./App.css";

const StyledApp = styled.div`
    font-family: "Kumbh Sans", sans-serif;
    background-color: ${(props) =>
        props.theme === "light" ? "#F2F2F2" : "#121721"};
    height: ${(props) => (props.isFilterApplied() ? "100vh" : "100%")};


    @media all and (min-width: 1440px) {
        position: relative;
        padding-bottom: 8rem;
    }
`;

function App() {
    const [theme, setTheme] = useState("light");
    const [titleFilterInput, setTitleFilterInput] = useState("");
    const [locationFilterInput, setLocationFilterInput] = useState("");
    const [isFullTimeChecked, setIsFullTimeChecked] = useState(false);
    const [isDetailsOpen, setIsDetailsOpen] = useState(false);
    const [activeJob, setActiveJob] = useState(jobs[0]);

    // * Title filter
    const onChangeTitleInput = (e) => {
        setTitleFilterInput(e.target.value);
    };

    // * Location filter
    const onChangeLocationInput = (e) => {
        setLocationFilterInput(e.target.value);
    };

    // * Full time filter
    const handleCheck = () => {
        setIsFullTimeChecked((prevState) => !prevState);
    };

    // * Change theme
    const changeTheme = () => {
        setTheme(theme === "light" ? "dark" : "light");
    };

    // * Checks if some filter is apllied
    const isFilterApplied = () => {
        if (
            titleFilterInput !== "" ||
            locationFilterInput !== "" ||
            isFullTimeChecked
        ) {
            return true;
        }
        return false;
    };

    // * Open job details
    const showDetails = (props) => {
        setIsDetailsOpen(true);
    };

    // * Close job details
    const hideDetails = () => {
        setIsDetailsOpen(false);
    };

    const getActiveJob = (id) => {
        const active = jobs.filter((job) => {
            return job.id === id;
        });
        setActiveJob(active);
        showDetails();
    };

    return (
        <StyledApp isFilterApplied={isFilterApplied} theme={theme}>
            <Header changeTheme={changeTheme} hideDetails={hideDetails} />
            {!isDetailsOpen && (
                <Main
                    theme={theme}
                    onChangeTitleInput={onChangeTitleInput}
                    onChangeLocationInput={onChangeLocationInput}
                    titleValue={titleFilterInput}
                    locationValue={locationFilterInput}
                    isFullTimeChecked={isFullTimeChecked}
                    onCheck={handleCheck}
                    jobs={jobs}
                    showDetails={showDetails}
                    getActiveJob={getActiveJob}
                />
            )}
            {isDetailsOpen && <Details activeJob={activeJob} theme={theme} />}
        </StyledApp>
    );
}

export default App;
