import React from "react";
import styled from "styled-components";

const StyledCard = styled.div`
    border-radius: 0.9375rem;
    background-color: ${(props) =>
        props.theme === "light" ? "#FFF" : "#19202D"};
    position: relative;
    bottom: 1rem;
    box-shadow: 0 4px 4px 0 #00000050;
    margin-bottom: 1.5rem;

    @media all and (min-width: 768px) {
        bottom: 3rem;
        margin-bottom: 0;
    }

`;

const Container = styled.section`
    display: grid;
    grid-template-columns: 1fr;
    place-items: center;

    .logo {
        border-radius: 15px;
        width: 50px;
        height: 50px;
        position: relative;
        bottom: 1.5rem;
        display: grid;
        place-items: center;

        @media all and (min-width: 768px) {
            grid-column: 1 / 2;
            grid-row: 1 / 3;
            border-radius: 0;
            height: 140px;
            width: 140px;
            position: static;
        }
    }

    .company-logo {
        @media all and (min-width: 768px) {
            width: 5rem;
        }
    }

    .company,
    .company-url {
        @media all and (min-width: 768px) {
            grid-column: 2 / 3;
            margin: 0;
        }
    }

    .company {
        font-size: 1.25rem;
        margin-bottom: 0.8125rem;
        color: ${(props) => (props.theme === "light" ? "#000" : "#FFF")};

        @media all and (min-width: 768px) {
            margin: 0;
            align-self: end;
            font-size: 1.5rem;
        }
    }

    .company-url {
        color: #6e8098;
        font-size: 1rem;
        margin-bottom: 1.6875rem;

        @media all and (min-width: 768px) {
            margin: 0;
            align-self: start;
        }
    }

    .company-site {
        padding: 1rem 1.25rem;
        color: #5964e0;
        background-color: #5964e020;
        border-radius: 5px;
        text-decoration: none;
        margin-bottom: 2rem;
        font-weight: 700;
        transition: all 0.25s  ;

        @media all and (min-width: 768px) {
            grid-row: 1 / 3;
            grid-column: 3 / 4;
            margin: 0;
            justify-self: center;
        }

        @media all and (min-width: 1440px) {
            &:hover {
                background-color: #C9C9C9;
            }
        }
    }

    @media all and (min-width: 768px) {
        grid-template-columns: 0.35fr 1fr 0.6fr;
        grid-template-rows: 1fr 1fr;
        row-gap: 1rem;
        column-gap: 2rem;
        align-items: center;
        justify-items: start;
    }
`;

export default function DetailsTop(props) {
    const src = props.activeJob[0].logo.slice(2);
    console.log(props.activeJob[0].logoBackground);

    return (
        <StyledCard theme={props.theme}>
            <Container theme={props.theme}>
                <div
                    className="logo"
                    style={{
                        backgroundColor: props.activeJob[0].logoBackground,
                    }}
                >
                    <img
                        className="company-logo"
                        src={require(`../${src}`).default}
                        alt="logo"
                    />
                </div>
                <h2 className="company">{props.activeJob[0].company}</h2>
                <span className="company-url">{`${props.activeJob[0].company
                    .split(" ")
                    .join("-")
                    .toLowerCase()}.com`}</span>
                <a className="company-site" href={props.activeJob[0].website}>
                    Company Site
                </a>
            </Container>
        </StyledCard>
    );
}
