import React from "react";
import styled from "styled-components";

import locationIcon from "../assets/desktop/icon-location.svg";
import searchIcon from "../assets/desktop/icon-search.svg";

    const Container = styled.div`
        display: flex;
        align-items: center;
        border-bottom: ${props => props.isModalOpen ? "1px solid #6E809830" : "none"};
        padding-bottom: ${props => props.isModalOpen ? "1.5rem" : "0"};
        
        @media all and (min-width: 1440px) {
            border-right: 1px solid #6E8098;
            padding-block: 1.6rem;
        }
    `;

    const Img = styled.img`
        margin-right: 10px;
    `;
    const Input = styled.input`
        border: none;
        outline: none;
        width: 100%;
        background-color: inherit;
        color: inherit;

    `;
    
export default function InputFilter(props) {
    return (
        <Container isModalOpen={props.isModalOpen}>
            <Img src={props.filter === "title" ? searchIcon : locationIcon} />
            <Input
                type="text"
                name={`filter-${
                    props.filter === "title" ? "title" : "location"
                }`}
                id={`filter-${props.filter === "title" ? "title" : "location"}`}
                placeholder={`Filter by ${
                    props.filter === "title" ? "title" : "location"
                }...`}
                value={
                    props.filter === "title"
                        ? props.titleValue
                        : props.locationValue
                }
                onChange={
                    props.filter === "title"
                        ? (e) => props.onChangeTitleInput(e)
                        : (e) => props.onChangeLocationInput(e)
                }
            />
        </Container>
    );
}
