import React from "react";
import Button from "./Button";
import styled from "styled-components";

const LinkContainer = styled.div`
    grid-column: 2 / 3;

`;

export default function Link(props) {
    return (
        <LinkContainer>
            <a href={props.href}>
                <Button>{props.value}</Button>
            </a>
        </LinkContainer>
    );
}
