import React from 'react'
import SearchBar from './SearchBar'
import JobList from './JobList'
import styled from 'styled-components'

const StyledMain = styled.div`
    margin-inline: 6.4%;

    @media all and (min-width: 768px) {
        margin-inline: 5.2%;
    }

    @media all and (min-width: 1440px) {
        margin-inline: 11.4%;
    }
`

export default function Main(props) {
    return (
        <StyledMain>
            <SearchBar
                theme={props.theme}
                onChangeTitleInput={props.onChangeTitleInput}
                onChangeLocationInput={props.onChangeLocationInput}
                titleValue={props.titleFilterInput}
                locationValue={props.locationFilterInput}
                isFullTimeChecked={props.isFullTimeChecked}
                onCheck={props.onCheck}
            />
            <JobList
                jobs={props.jobs}
                theme={props.theme}
                titleValue={props.titleValue}
                locationValue={props.locationValue}
                isFullTimeChecked={props.isFullTimeChecked}
                showDetails={props.showDetails}
                getActiveJob={props.getActiveJob}
            />
        </StyledMain>
    )
}
