import React from "react";
import styled from "styled-components";
import Link from "./Link";

const Footer = styled.footer`
    grid-column: 1 / 3;
    background-color: ${(props) =>
        props.theme === "light" ? "#FFF" : "#19202D"};
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: center;
    margin: 2rem -6.4% 0;
    padding: 1.375rem 6.4%;

    @media all and (min-width: 1440px) {
        margin-top: 4rem;
        width: 100%;
        position: absolute;
        margin: 0;
        left: 0; 
        bottom: 0;
        padding-inline: 11.4%;
    }

    span {
        color: #6e8098;
        grid-column: 1 / 2;

        @media all and (max-width: 767px) {
            display: none;
        }
    }
`;
const Position = styled.h2`
    color: ${(props) => (props.theme === "light" ? "#19202D" : "#FFF")};
    font-size: 1.25rem;
    margin-bottom: 0.75rem;
    grid-column: 1 / 2;

    @media all and (max-width: 767px) {
        display: none;
    }
`;

const LinkContainer = styled.div`
    grid-column: 3 / 4;
    grid-row: 1 / 3;

    @media all and (min-width: 1440px) {
        width: 50%;
        justify-self: end;
    }
`;

export default function DetailsFooter(props) {
    return (
        <Footer theme={props.theme}>
            <Position theme={props.theme}>{props.position}</Position>
            <span>So Digital Inc.</span>
            <LinkContainer>
                <Link href={props.href} value={props.value}></Link>
            </LinkContainer>
        </Footer>
    );
}
