import { Container } from "@material-ui/core";
import React from "react";
import styled from "styled-components";
import OrderedList from "./OrderedList";
import UnorderdList from "./UnorderdList";

const DescriptionContainer = styled.article`
    * {
        color: ${(props) => (props.theme === "light" ? "#6E8098" : "#9DAEC2")};
    }

    grid-column: 1 / 3;
`

const Paragraph = styled.p`
    font-size: 1rem;
    line-height: 26px;
    margin-top: 2rem;
    margin-bottom: 2.5rem;
`;

const Heading = styled.h2`
    font-size: 1.25rem;
    line-height: 20px;
    color: ${(props) => (props.theme === "light" ? "#19202D" : "#FFF")};
`;

export default function JobDescription(props) {
    return (
        <DescriptionContainer theme={props.theme}>
            <Paragraph>{props.description}</Paragraph>
            <Heading theme={props.theme}>Requirements</Heading>
            <Paragraph>{props.requirementsContent}</Paragraph>
            <UnorderdList items={props.requirementsItems} />
            <Heading theme={props.theme}>What You Will Do</Heading>
            <Paragraph>{props.roleContent}</Paragraph>
            <OrderedList items={props.roleItems}></OrderedList>
        </DescriptionContainer>
    );
}
