import React from "react";
import styled from "styled-components";

import sunIcon from "../assets/desktop/icon-sun.svg";
import moonIcon from "../assets/desktop/icon-moon.svg";


    const ThemeContainer = styled.div`
        display: grid;
        grid-template-columns: 0.4fr 1fr 0.4fr;
        column-gap: 1rem;
        justify-content: space-between;
        align-items: center;
    
        .sun {
            width: 20px;
            height: 18px;
        }
    
        .moon {
            width: 12px;
            height: 12px;
        }
    `;
    
    const StyledThemeSwitch = styled.div`
        width: 48px;
        height: 24px;
        background-color: #fff;
        border-radius: 20px;
        position: relative;
    
        input[type="checkbox"] {
            visibility: hidden;
    
            &:checked + label::after {
                left: 29px;
            }
    
            &:checked {
                background: black;
            }
        }
    
        label {
            position: absolute;
            inset: 0;
            width: 100%;
            display: inline-block;
            cursor: pointer;
    
            &:hover::after {
                background-color: #939bf4;
            }
    
            &::after {
                content: "";
                width: 14px;
                height: 14px;
                background: #5964e0;
                position: absolute;
                border-radius: 50%;
                top: 50%;
                left: 5px;
                transform: translateY(-50%);
                transition: left 0.3s, background-color 0.2s;
            }
        }
    `;

export default function ThemeSwitch(props) {
    return (
        <ThemeContainer>
            <img className="icon sun" src={sunIcon} alt="light theme icon" />
            <StyledThemeSwitch>
                <input
                    onChange={props.changeTheme}
                    type="checkbox"
                    name="theme-switch"
                    id="theme-switch"
                />
                <label htmlFor="theme-switch"></label>
            </StyledThemeSwitch>
            <img className="icon moon" src={moonIcon} alt="dark theme icon" />
        </ThemeContainer>
    );
}
