import React, { useState } from "react";
import styled from "styled-components";
import ReactModal from "react-modal";

import Button from "./Button";
import InputFilter from "./InputFilter";
import CheckboxFilter from "./CheckboxFilter";

import filterIcon from "../assets/mobile/icon-filter.svg";

const StyledSearchBar = styled.form`
    background-color: ${(props) =>
        props.theme === "light" ? "#fff" : "#19202D"};
    border-radius: 5px;
    display: grid;
    grid-template-columns: 1fr 0.3fr 0.3fr;
    align-items: center;
    justify-content: space-between;
    position: relative;
    bottom: 40px;
    padding: 1rem;
    color: ${props => props.theme === "dark" ? "#FFF" : "#000"};

    
    @media all and (min-width: 768px) {
        grid-template-columns: 1fr 1fr 1fr 0.25fr;
    }

    @media all and (min-width: 1440px) {
        column-gap: 1rem;
        grid-template-columns: 1.5fr 1fr 0.5fr 0.25fr;
    }

    & input {
        background-color: inherit;
        border: none;
        font-family: inherit;
        height: 20px;
        color: ${(props) => (props.theme === "dark" ? "#FFF" : "initial")};

        &[type="text"] {
            outline: none;
        }

        &::placeholder {
            font-size: 1rem;
        }

        &:first-of-type {
            border-radius: 5px 0 0 5px;
        }
    }

    .filter-button {
        justify-self: center;
    }

    & .full-time-container {
        display: flex;
        align-items: center;

        /* input[type="checkbox"] {
            width: 24px;
            margin-right: 10px;
            position: relative;

            &::after {
                content: "";
                position: absolute;
                inset: 0;
                background-color: #19202d;
                opacity: 0.1;
            }
        } */
    }

`;

export default function SearchBar(props) {
    const [isModalOpen, setIsModalOpen] = useState(false);

    const openModal = () => {
        setIsModalOpen((prev) => !prev);
    };

    const closeModal = () => {
        setIsModalOpen(false);
    };

    return (
        <StyledSearchBar
            onSubmit={(e) => e.preventDefault()}
            theme={props.theme}
        >
            <InputFilter
                isModalOpen={false}
                filter="title"
                onChangeTitleInput={props.onChangeTitleInput}
                titleValue={props.titleValue}
            />
            {window.innerWidth < 768 && (
                <>
                    <div onClick={openModal} className="filter-button">
                        <img src={filterIcon} alt="filter button" />
                    </div>
                    <ReactModal
                        isOpen={isModalOpen}
                        onRequestClose={closeModal}
                        style={{
                            overlay: {
                                backgroundColor: "rgba(0, 0, 0, 0.75)",
                            },
                            content: {
                                height: "220px",
                                width: "300px",
                                top: "50%",
                                left: "50%",
                                transform: "translate(-50%, -50%)",
                                backgroundColor:
                                    props.theme === "dark"
                                        ? "#19202D"
                                        : "#F2F2F2",
                                border: "none",
                                color: props.theme === "dark" ? "#FFF" : "#000",
                            },
                        }}
                        ariaHideApp={false}
                    >
                        <InputFilter
                            isModalOpen={true}
                            filter="location"
                            onChangeLocationInput={props.onChangeLocationInput}
                            locationValue={props.locationValue}
                        />
                        <CheckboxFilter
                            isChecked={props.isFullTimeChecked}
                            onChange={props.onCheck}
                        />
                        <Button onClick={closeModal}>Search</Button>
                    </ReactModal>
                </>
            )}
            {window.innerWidth >= 768 && (
                <>
                    <InputFilter
                        filter="location"
                        onChangeLocationInput={props.onChangeLocationInput}
                        locationValue={props.locationValue}
                    />
                    <div className="full-time-container">
                        <CheckboxFilter
                            isChecked={props.isFullTimeChecked}
                            onChange={props.onCheck}
                            theme={props.theme}
                        />
                    </div>
                    {/* <Button>Search</Button> */}
                </>
            )}
            <Button icon>Search</Button>
        </StyledSearchBar>
    );
}
