import React from "react";
import styled from "styled-components";

const StyledInfo = styled.div`
    position: relative;

    .posted-at,
    .contract,
    .company-name {
        font-size: 1rem;
        color: #6e8098;
    }

    .posted-at {
        margin-right: 0.6rem;
    }

    .contract {
        &::before {
            content: ".";
            font-size: 2.75rem;
            position: relative;
            margin-right: 10px;
            bottom: 3px;
        }
    }

    .position {
        font-size: 1.25rem;
        margin-top: 0.8125rem;
        margin-bottom: 1.0625rem;
        color: ${props => props.theme === "light" ? "#19202D" : "#FFF"};

        @media all and (min-width: 768px) {
            font-size: 1.75rem;
        };
    }

    .company-name {
        font-weight: 400;
    }

    .location {
        color: #5964e0;
        font-size: 0.875rem;
        margin-top: 2.75rem;

        @media all and (min-width: 768px) {
            margin-top: 1.375rem;
        }
    }

    @media all and (min-width: 768px) {
        grid-column: 1 / 2;
    }
`;

export default function JobMainInfo(props) {
    return (
        <StyledInfo theme={props.theme}>
            <span className="posted-at">{props.postedAt}</span>
            <span className="contract">{props.contract}</span>
            <h2 className="position">{props.position}</h2>
            <h4 className="company-name">{props.company}</h4>
            <h4 className="location">{props.location}</h4>
        </StyledInfo>
    );
}
