import { render } from "@testing-library/react";
import React from "react";
import styled from "styled-components";

import ThemeSwitch from "./ThemeSwitch";

import headerMobileBG from "../assets/mobile/bg-pattern-header.svg";
import headerTabletBG from "../assets/tablet/bg-pattern-header.svg";
import headerDesktopBG from "../assets/desktop/bg-pattern-header.svg";
import logo from '../assets/desktop/logo.svg'

const StyledHeader = styled.header`
    background-image: url(${headerMobileBG});
    background-size: cover;
    display: flex;
    justify-content: space-between;
    padding: 2rem 6.4% 4.5rem;

    .logo {
        color: #fff;

        @media all and (min-width: 1440px) {
            &:hover {
                cursor: pointer;
            }
        }
    }

    @media all and (min-width: 768px) {
        background-image: url(${headerTabletBG});
        padding: 2.625rem 5.2% 5.375rem;
    }

    @media all and (min-width: 1440px) {
        background-image: url(${headerDesktopBG});
        padding: 2.8125rem 11.4% 5.3125rem;
    }
`;

export default function Header(props) {
    return (
        <StyledHeader>
            <img onClick={props.hideDetails} src={logo} className="logo" alt="logo"/>
            <ThemeSwitch changeTheme={props.changeTheme} />
        </StyledHeader>
    );
}
