import React from "react";
import JobCard from "./JobCard";
import styled from "styled-components";

const Container = styled.div`
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    column-gap: 1.5rem;

    @media all and (min-width: 1440px) {
        grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
        column-gap: 3rem;
    }
`;

export default function JobList(props) {
    const filteredJobs = props.jobs.filter((job) => {
        return (
            (job.position
                .toLowerCase()
                .includes(props.titleValue.toLowerCase()) ||
            job.company
                .toLowerCase()
                .includes(props.titleValue.toLowerCase())) &&
            job.location
                .toLowerCase()
                .includes(props.locationValue.toLowerCase()) &&
            (props.isFullTimeChecked
                ? job.contract === "Full Time"
                : job.contract)
        );
    });

    const showJobs = (filter) => {
        return filter.map((job) => {
            let src = job.logo.slice(2);

            return (
                <JobCard
                    key={job.id}
                    id={job.id}
                    imgSrc={src}
                    postedAt={job.postedAt}
                    contract={job.contract}
                    position={job.position}
                    company={job.company}
                    location={job.location}
                    logoBackground={job.logoBackground}
                    theme={props.theme}
                    showDetails={props.showDetails}
                    getActiveJob={props.getActiveJob}
                />
            );
        });
    };

    return <Container>{showJobs([...new Set(filteredJobs)])}</Container>;
}
