import React from 'react'
import styled from 'styled-components'

const StyledOrderdList = styled.ol`
    margin-bottom: 3rem;
    counter-reset: li;
    list-style-position: outside;
    margin-inline: 1rem;
`

const ListItem = styled.li`
    color: ${props => props.theme === "light" ? "#6E8098" : "#9DAEC2"};
    margin-bottom: 1rem;
    padding-left: 1rem;

    &::marker {
        color: #5964E0;
    }

`;

export default function OrderedList(props) {
    return (
        <StyledOrderdList>
            {props.items.map((item, index) => {
                return <ListItem key={index}>{item}</ListItem>;
            })}
        </StyledOrderdList>
    )
}
