import React from "react";
import styled from "styled-components";

const Container = styled.div`
    display: flex;
    align-items: center;
    font-family: inherit;
    margin-block: 1.5rem;

    label {
        font-family: 'Kumbh Sans';
    }
`;

const Checkbox = styled.input.attrs({type: "checkbox"})`
    height: 20px;
    width: 20px;
    border: none;
    background: black;
    position: relative;
    margin-right: 0.5rem;
    visibility: 0;

    &:checked::before {
        display: none;
    }

    &::before {
        content: "";
        position: absolute;
        inset: 0;
        background-color: #979797;
        border-radius: 5px;
    }
`

export default function CheckboxFilter(props) {
    return (
        <Container>
            <Checkbox
                name="filter-full-time"
                id="filter-full-time"
                checked={props.isChecked}
                onChange={props.onChange}
            />
            <label htmlFor="filter-full-time">
                <strong>Full Time Only</strong>
            </label>
        </Container>
    );
}
