import React from "react";
import styled from "styled-components";
import JobMainInfo from "./JobMainInfo";

const StyledJobCard = styled.article`
    padding: 1.875rem 2rem 2rem;
    position: relative;

    @media all and (min-width: 768px) {
        padding: 2rem;
    }

    @media all and (min-width: 1440px) {
        &:hover {
            cursor: pointer;
        }
    }


    .logo {
        background-color: ${(props) => props.logoBackground};
        border-radius: 15px;
        width: 50px;
        height: 50px;
        position: absolute;
        transform: translateY(-100%);
        display: grid;
        place-items: center;
    }
`;

const Card = styled.div`
    border-radius: 6px;
    box-shadow: 0 4px 4px 0 #00000024;
    margin-bottom: 50px;
    background-color: ${(props) =>
        props.theme === "light" ? "#FFF" : "#19202D"};
    color: ${(props) => (props.theme === "light" ? "initial" : "#FFF")};
    /* height: 251px; */
`;

export default function JobCard(props) {
    return (
        <Card theme={props.theme}>
            <StyledJobCard
                onClick={() => props.getActiveJob(props.id)}
                logoBackground={props.logoBackground}
            >
                <div className="logo">
                    <img
                        className="company-logo"
                        src={require(`../${props.imgSrc}`).default}
                        alt="company logo"
                    />
                </div>
                <JobMainInfo
                    theme={props.theme}
                    postedAt={props.postedAt}
                    contract={props.contract}
                    position={props.position}
                    company={props.company}
                    location={props.location}
                />
            </StyledJobCard>
        </Card>
    );
}
