import React from "react";
import JobMainInfo from "./JobMainInfo";
import Card from "./Card";
import Link from "./Link";
import JobDescription from "./JobDescription";
import styled from "styled-components";

const LinkContainer = styled.div`
    display: grid;
    place-items: center;
`;

export default function JobInfo(props) {
    return (
        <Card theme={props.theme}>
            <JobMainInfo
                theme={props.theme}
                postedAt={props.postedAt}
                contract={props.contract}
                position={props.position}
                company={props.company}
                location={props.location}
            />
            <LinkContainer>
                <Link href={props.href} value="Apply now" />
            </LinkContainer>
            <JobDescription
                theme={props.theme}
                description={props.description}
                requirementsContent={props.content}
                requirementsItems={props.requirementsItems}
                roleContent={props.roleContent}
                roleItems={props.roleItems}
            />
            {/* <DetailsFooter
                position={props.position}
                theme={props.theme}
                href={props.href}
                value="Apply now"
            /> */}
        </Card>
    );
}
